.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  &__form {
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    width: 400px;

    & input {
      margin: 0.8rem 0;
      padding: 1rem 0.8rem;
      border-radius: 15px;
    }

    & label {
      font-size: 1.6rem;

      &:last-of-type {
        margin-top: 1rem;
      }
    }

    & .btn {
      width: 400px;
      margin-top: 1.8rem;
      border-radius: 15px;
      padding: 1rem;
    }
  }
}

.no-user {
  width: 100%;
  margin-top: 20rem;

  & h1 {
    text-align: center;
    font-size: 4rem;
  }
}
